import { MoreVertical } from "lucide-react";

export default function CategoriesPage() {
  const categories = [
    { id: 1, name: "Clothing", productsCount: 12, status: "Active" },
    { id: 2, name: "Accessories", productsCount: 8, status: "Active" },
    { id: 3, name: "Electronics", productsCount: 0, status: "Inactive" },
  ];

  return (
    <div className="p-8 flex-1">

      {/* PAGE HEADER */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Categories</h1>
          <p className="text-gray-500 mt-1">Manage all product categories in Threadix store.</p>
        </div>

        <button className="px-5 py-2.5 bg-black text-white font-medium rounded-xl hover:bg-gray-900 transition">
          + Add Category
        </button>
      </div>

      {/* SEARCH */}
      <div className="bg-white p-5 rounded-xl border shadow-sm mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <input
          type="text"
          placeholder="Search categories..."
          className="w-full md:w-72 px-4 py-2.5 border rounded-xl focus:ring-2 focus:ring-black outline-none transition"
        />
      </div>

      {/* CATEGORIES TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-hidden">
        <table className="w-full min-w-[600px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Category</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Products Count</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-right px-6 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>

          <tbody>
            {categories.map((cat) => (
              <tr key={cat.id} className="border-b last:border-none hover:bg-gray-50 transition">
                <td className="px-6 py-4 font-semibold text-gray-900">{cat.name}</td>
                <td className="px-6 py-4 text-gray-700">{cat.productsCount}</td>
                <td className="px-6 py-4">
                  <span
                    className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                      cat.status === "Active"
                        ? "bg-green-100 text-green-700"
                        : "bg-red-100 text-red-700"
                    }`}
                  >
                    {cat.status}
                  </span>
                </td>
                <td className="px-6 py-4 text-right">
                  <button className="p-2 hover:bg-gray-200 rounded-full transition">
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
